
@import url(./style/reset.css);
.header {
  background: #22252D;
  position: fixed;
  width: 100%;
  left: 0;
  top: 0;
  z-index: 3;
  color: #fff;
}
.header .header_content {
    max-width: 1200px;
    margin: 0 auto;
    overflow: hidden;
    padding: 16px 0 17px;
}
.header a {
    color: #fff;
    display: block;
    float: left;
    padding: 0 39.5px;
    text-align: center;
}
.header .logo {
    width: 66px;
    height: 66px;
    float: left;
    background: url(./img/Artboard.png);
    margin-right: 9px;
}
.header .list {
    float: left;
}
.header .list h3 {
      font-size: 22px;
}
.header .list span {
      font-size: 14px;
      opacity: 0.8;
      font-weight: 100;
}
.header .last {
    margin-left: 275px;
}
.content {
  position: fixed;
  left: 0;
  width: 100%;
  top: 99px;
  height: calc( 100vh - 99px);
  overflow: auto;
  overflow-x: hidden;
}
@media screen and (max-width: 980px) {
.header .header_content {
    max-width: 980px;
}
.header .last {
    margin-left: 50px;
}
.logo {
    margin-left: 12px;
}
}



